import { Body, Controller, Post, Res } from '@nestjs/common';
import { Response } from 'express';
import { AuthService } from './auth.service';
import { LoginDto } from './dto/login.dto';
import { SignupDto } from './dto/signup.dto';

@Controller('auth')
export class AuthController {
  constructor(private readonly auth: AuthService) {}

  @Post('signup')
  async signup(@Body() body: SignupDto, @Res() res: Response) {
    const access_token = await this.auth.signup(body.email, body.password);
    return res.status(201).json({ access_token });
  }

  @Post('login')
  async login(@Body() body: LoginDto, @Res() res: Response) {
    const { token, created } = await this.auth.login(body.email, body.password);
    // 201 if we *created* the user during login, else 200
    return res.status(created ? 201 : 200).json({ access_token: token });
  }
}